@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,300;1,400;1,500&display=swap);

.app__logo img {
  color: white;
}
.app__navbar {
  padding: 1.5rem 0;
}
.app__navbar-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app__navbar-menu-links {
  display: flex;
  margin-bottom: 0;
  grid-gap: 1rem;
  gap: 1rem;
  list-style: none;
}

.app__navbar-menu-links li a {
  color: #fff;
  font-family: var(--font_base);
  font-weight: 700;
  padding: 0 10px;
  text-decoration: none;
  transition: 0.4s all ease-in;
}
.app__navbar-menu-links li :hover {
  color: #007bff;
}

.app__navbar-login h5 {
  color: #007bff;
  transition: 0.4s all ease-in;
  cursor: pointer;
  margin-bottom: 0;
}

.app__navbar-login :hover {
  color: white;
}

@media (max-width: 970px) {
  .app__navbar-menu-links {
    display: none;
  }
} ;

.app__header {
  padding-top: 10rem;
}
.app__grid {
  padding: 5rem 0;

  background-color: #fff;
  margin-top: 3rem;
}

.app__header-content-title {
  text-align: center;
  color: #fff;
  text-transform: capitalize;
  font-family: var(--font_base);
  font-size: 3.8rem;
  margin-bottom: 1.5rem;
}

.app__header-content-subtitle {
  text-align: center;
  color: #fff;
  font-weight: 400;
  font-size: 1.7rem;
  font-family: var(--font_base);
  text-transform: capitalize;
  margin-bottom: 4.5rem;
}
.app__specs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 6rem;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

.hero_image img {
  max-width: 100%;
}
.app__btn {
  text-align: center;
}
.app__btn .discover_more {
  padding: 1rem 2rem;
  border: none;
  background-color: #4a7aec;
  font-size: 14px;
  font-family: var(--font_base);
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  border-radius: 70px;
  margin-top: 4rem;
}

@media (max-width: 970px) {
  .app__header-content-title {
    font-size: 2.2rem;
  }

  .app__header-content-subtitle {
    font-size: 1.5rem;
  }
}

@media (max-width: 760px) {
  .app__specs {
    grid-template-columns: repeat(1, 1fr);
  }
} ;

.app__specification-grid_box h3 {
  font-size: 60px;
  font-weight: 900;
  color: #4a7aec;
  text-align: center;
}
.app__specification-grid_box .tagline {
  font-family: var(--font_base);
  font-weight: 700;
  color: #404551;
  line-height: 1.2;
  text-align: center;
  font-size: 24px;
}
.app__specification-grid_box .description {
  font-family: var(--font_base);
  color: #abadbe;
  font-size: 15px;
  font-weight: 500;
  margin-top: 0;
  text-align: center;
  line-height: 1.9;
}
.clients_img {
  display: block;
  margin: 0 auto;
  margin-bottom: 1rem;
}
.app__specification-grid1 {
  padding: 2rem 0;

  transition: 0.3s all ease-in;
}
.app__specification-grid1:hover {
  box-shadow: rgba(149, 157, 165, 0.5) 0px 10px 34px;
  cursor: pointer;
}

.small__title {
  font-weight: 700;
  color: #bec0cc;
  font-size: 12px;
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
}
.large__title {
  font-size: 40px;
  text-align: center;
  margin: 0;
  font-weight: 900;
  font-family: var(--font_base);
  color: #404551;
}
.small__title1 {
  font-weight: 700;
  color: #bec0cc;
  font-size: 12px;
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.large__title1 {
  font-size: 40px;

  margin: 0;
  font-weight: 900;
  font-family: var(--font_base);
  color: #fff;
}

@media (max-width: 616px) {
  .small__title,
  .small__title1 {
    font-size: 12px;
  }
  .large__title,
  .large__title1 {
    font-size: 30px;
  }
}

.app__clients {
  background-color: #fff;
  margin: 4rem 0;
}
.app__clients-logos {
  margin-top: 4rem;
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  justify-content: space-around;
  flex-wrap: wrap;
}
.app__clients-logos > img {
  max-width: 170px;
  height: 42px;
}

.app__clients-get {
  background-color: #211b31;
  padding-top: 7rem;
  margin: 3rem 0;
}
.app__clients-get .info .para {
  font-family: var(--font_base);
  color: #abadbe;
  font-size: 15px;
  font-weight: 500;
  margin-top: 0;
  line-height: 1.9;
  margin-top: 2rem;
}
.app__getClients-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10rem;
  gap: 10rem;
  padding-bottom: 3rem;
}

.featured_img img {
  max-width: 100%;
  height: auto;
}
.app_bar_chart {
  margin: 2rem 0;
}
.app_bar_chart .chart {
  margin: 1rem 0;
}

.app_bar_chart .chart p {
  font-family: "Roboto", sans-serif;
  color: #abadbe;
  font-size: 15px;
  font-weight: 500;
  margin-top: 0;
  line-height: 1.9;
}

.bar {
  height: 5px;
  background-color: #fff;
}
.fill1 {
  width: 70%;
  height: 5px;
  display: block;
  background-color: #4a7aec;
}

.fill2 {
  width: 90%;
  height: 5px;
  display: block;
  background-color: #4a7aec;
}
.fill3 {
  width: 60%;
  height: 5px;
  display: block;
  background-color: #4a7aec;
}
.app__grid_clients {
  background-color: #fff;
}
.app__grid_clients .clients_grid_box {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  gap: 2rem;
}
@media (max-width: 660px) {
  .app__getClients-content {
    grid-template-columns: repeat(1, 1fr);
    grid-row-gap: 1rem;
    row-gap: 1rem;
  }
  .app__grid_clients .clients_grid_box {
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 1rem;
    row-gap: 1rem;
  }
}
@media (max-width: 470px) {
  .app__grid_clients .clients_grid_box {
    grid-template-columns: repeat(1, 1fr);
    grid-row-gap: 1rem;
    row-gap: 1rem;
  }
} ;

.list {
  margin-top: 4rem;
  display: flex;
  justify-content: center;
  list-style: none;
  flex-wrap: wrap;
}

.list li {
  font-family: "Roboto", sans-serif;
  color: #abadbe;
  font-size: 15px;
  font-weight: 500;
  margin-top: 0;
  line-height: 1.9;
  padding: 0 0.6rem;
  transition: 0.5s all ease-in;
}

.list li:hover {
  color: #007bff;
  cursor: pointer;
}
.grid_images {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-top: 4rem;
}
.grid_images .image {
  overflow: hidden;
}
.grid_images img {
  width: 100%;

  margin-top: 0;
  transition: 0.4s all ease-in;
}

.grid_images img:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

@media (max-width: 570px) {
  .grid_images {
    grid-template-columns: repeat(2, 1fr);
  }
}

.app__workflow-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 5rem;
  grid-gap: 4rem;
  gap: 4rem;
}
.app__workflow {
  padding: 5rem 0;
}

@media (max-width: 760px) {
  .app__workflow-grid {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
  }
} ;

.app__shout {
  border: 1px solid black;
  padding: 5rem 0;
  background-color: rgba(33, 27, 49, 0.88);
  text-align: center;
}

.contact_us {
  background-color: #4a7aec;
  padding: 0 30px;
  font-size: 16px;
  font-weight: 700;
  color: #fff;

  line-height: 3;
  margin-top: 4.5rem;
  border: none;
  font-family: var(--font_base);
  border-radius: 70px;
}

:root {
  --font_base: "Roboto", sans-serif;
}

* {
  margin: 0;
  padding: 0;
}

.container1 {
  width: 95%;
  margin: 0 auto;
}

.app_main {
  background: linear-gradient(
    to bottom,
    rgb(20, 5, 87) 0%,
    rgb(68, 91, 238) 100%
  );
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

@media (max-width: 750px) {
  .container1 {
    width: 85%;
    margin: 0 auto;
  }
} ;

